
html {
    width: 100%;
    height: 100%;
    display: table;
    font-family:"Museo Sans";
    letter-spacing: 1px;
    font-size: 1rem;
    padding: 0px;
    overflow-y: hidden;
}

body {
    width: 100%;
    height: 100%;
    margin: 0px;
}

#root{
    width: 100%;
    height: 100%;
}

a{
    text-decoration: none;
    color: inherit;
    margin: 0;
}

.viewAllLink{
    width: 70px;
}

@font-face {
    font-family:"Museo Sans";
    src: url(/static/media/museoSans.5b9453f7.ttf) format("TrueType");
  }

@media(min-width: 651px) {
    .normalNav{ display: inline-block};
    .link{ display: inline-block };
    .hamburgerMenu{ display: none; };
}

@media(max-width: 650px) {
    .link{ display: none }
    .normalNav{ display: none; };
    .hamburgerMenu{ display: inline-block; }
}

/* PACKAGES */
