.App{
    height: 100%;
}
/* BURGER MENU */

/* Position and sizing of burger button */

.hamburgerMenu{
    position: fixed;
  }
  
  
  .bm-burger-button {
      position: relative;
      width: 36px;
      height: 30px;
      margin-right: 5%;
      right: 60%;
      outline: 0;
      border: none;
      outline: none !important;
    }

    .bm-burger-button:focus{
      outline: none !important;
    }

    .bm-burger-bars:hover{}
  
    @media(min-width: 651px) {
      .bm-burger-button{ display: none; }
  }
  
  
    /* Color/shape of burger icon bars */
    .bm-burger-bars {
      background: #373a47;
      -webkit-transition: all .15s ease-out;
      transition: all .15s ease-out;
    }
     
    /* Color/shape of burger icon bars on hover*/
    .bm-burger-bars-hover {
      background-color: #feda6a;
    }
     
  
    /* Color/shape of close button cross */
    .bm-cross {
      background-color: #feda6a;
      width: 25px;
      position: fixed;
    }
     
    /*
    Sidebar wrapper styles
    Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
    */
    .bm-menu-wrap {
      position: fixed;
      height: 100%;
      top: 0 !important;
      
    }
    @media(min-width: 651px) {
      .bm-menu-wrap{ display: none;}
  }
     
    /* General sidebar styles */
    .bm-menu {
      background: #373a47;
      padding: 20px 20px;
      font-size: 1.15em;
      /* margin-top: -1%; */
      /* margin-right: -1%; */
      height: 255px !important;
      position: fixed;
      /* top: 0; */
      /* right: 0; */
      overflow: hidden;
      display: flex;
      justify-content: flex-end;
      border-left: #d4d4dc solid 1px;
      border-bottom: #d4d4dc solid 1px;
      overflow: hidden;
      border-bottom-left-radius: 5px
    }
     
     
    /* Wrapper for item list */
    .bm-item-list {
      color: #b8b7ad;
      width: 110px
    }
     
    /* Individual item */
    .bm-item {
      display: inline-block;
      margin: 20%;
      margin-left: 0;
      margin-right: 0;
    }
     
    /* Styling of overlay */
    .bm-overlay {
      background: rgba(0, 0, 0, 0.2);
      margin-left: -31%;
      height: 271px !important;
      width: 180px !important;
      top: 0 !important;

  
    }
    @media(min-width: 651px) {
      .bm-overlay{ display: none; }
  }
  
    .menu-item{
        color: #d4d4dc;
        text-decoration: none;
        outline: none;
        -webkit-transition: all .3s ease-out;
        transition: all .3s ease-out;
    }
  
    .menu-item:hover{
      color:#879FFF;
    }
@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  100% {
    -webkit-transform: translateX(calc(-400px * 3));
            transform: translateX(calc(-400px * 3)); } }

@keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  100% {
    -webkit-transform: translateX(calc(-400px * 3));
            transform: translateX(calc(-400px * 3)); } }

@media (max-width: 600px) {
  @-webkit-keyframes scroll {
    100% {
      -webkit-transform: translateX(calc(-400px * 2));
              transform: translateX(calc(-400px * 2)); } }
  @keyframes scroll {
    100% {
      -webkit-transform: translateX(calc(-400px * 2));
              transform: translateX(calc(-400px * 2)); } } }

.slider {
  background: white;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
  height: 200px;
  margin: auto;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 1; }
  .slider::before, .slider::after {
    background: -webkit-gradient(linear, left top, right top, from(white), to(rgba(255, 255, 255, 0)));
    background: linear-gradient(to right, white 0%, rgba(255, 255, 255, 0) 100%);
    content: "";
    height: 200px;
    position: absolute;
    width: 200px;
    z-index: 2; }
    @media (max-width: 600px) {
      .slider::before, .slider::after {
        height: 250px;
        width: 100px; } }
  .slider .slide-track {
    -webkit-animation: scroll 40s linear infinite;
            animation: scroll 40s linear infinite;
    display: flex;
    width: calc(400px * 15); }
  .slider .slide {
    height: 200px;
    width: 700px;
    margin-right: 20px;
    margin-top: 10px;
    margin-left: 20px; }
  @media (max-width: 600px) {
    .slider .slide {
      width: 240px; } }

@media (max-width: 600px) {
  .slider {
    height: 250px; } }

